body {
    font-family: Arial, sans-serif;
    text-align: center;
}

#counter {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 20px;
}

#clicker {
    color: #fff;
    border-radius: 1000px;
    cursor: pointer;
}

#clicker:hover {
    background-color: #3e8e41;
}

#clicker:active {
    transition: 100;
    transform: scale(1.1);
}